<template>
  <div class="main-box">
    <el-row>
      <el-col :span="24" class="topTip"><div><i class="el-icon-warning"></i>您的华夏银行存管账户已经正式开通，款项会直接支付到您的银行存款账户；您可以随时从存管账户提现到您绑定的银行中，提现过程不会收取任何费用。</div></el-col>
    </el-row>
    <el-row class="accInfo">
      <el-col :span="24">
        <div class="left jigou">
          *所属机构：<el-select style="width: 30%;line-height: 40px;" v-model="value" placeholder="请选择">
          <el-option v-for="(item, index) in agencyObject"
            :key="index"
            :label="item.recoCode"
            :value="item.recoCode">
          </el-option>
        </el-select>
        </div>
      </el-col>
      <el-col :span="12"><div class="left">
        <div>
          <img src="/static/img/logo-bank.jpg">
          <div><span class="jiufu">华夏存管账户：{{huaxiazhanghu}}</span><span class="huaxia">存管子帐号(蜡笔)：{{labizhanghu}}</span></div></div>
      </div></el-col>
      <el-col :span="12">
        <div class="right">
          <div class="top" v-if="openaccstatus === 'P02'">
          <!--<div class="top" v-if="1">-->
            <el-button @click="changebankcard">变更银行卡</el-button>
            <el-button @click="changebankphone">更改手机号</el-button>
            <el-button @click="changePassword('modify')">修改交易密码</el-button>
            <el-button @click="changePassword('reset')">重置交易密码</el-button>
          </div>
          <div class="top" v-else-if="openaccstatus === 'P01'">
            <el-button :disabled="true" type="primary">开通存管账户</el-button>
          </div><div class="top" v-else>
            <el-button  @click="openAcc" type="primary">开通存管账户</el-button>
          </div>
          <div class="bottom">
            <p>开户状态：<span>{{openmsg}}</span></p>
            <p>认证状态：<span>{{acctstsmsg}}</span></p>
          </div>
        </div>
      </el-col>
    </el-row>
    <el-row class="accInfo1">
      <el-col :span="8">
        <p>企业简称：<span>{{qiyejiancheng}}</span></p>
        <p>企业名称：<span>{{qiyemingcheng}}</span></p>
        <p>开户时间：<span>{{kaihushijian}}</span></p>
        <p>银行卡：<span>{{yinhangka}}</span></p>
      </el-col>
      <el-col :span="8">
        <p>证件类型：<span>{{zhengjianleixing}}</span></p>
        <p>证件号码：<span>{{zhengjianhaoma}}</span></p>
        <p>营业执照编号：<span>{{zhizhaobianhao}}</span></p>
        <p>税务登记号：<span>{{dengjihao}}</span></p>
      </el-col>
      <el-col :span="8">
        <p>法人姓名：<span>{{farenxingming}}</span></p>
        <p>法人身份证号：<span>{{farenshenfenzheng}}</span></p>
        <p>联系人手机号：<span>{{farenshoujihao}}</span></p>
      </el-col>
    </el-row>
    <template v-if="openaccstatus === 'P02'">
      <el-row class="tab">
        <el-col :span="24">
          <div>
            <p :class="[tabActive==0?'active':'']" @click="changeTab(0)">待提现</p>
            <p :class="[tabActive==1?'active':'']" @click="changeTab(1)">提现中</p>
            <p :class="[tabActive==2?'active':'']" @click="changeTab(2)">已提现</p>
          </div>
        </el-col>
      </el-row>
      <el-row class="screening" v-if="tabActive==0">
        <el-col :span="10">
          <div>放款时间：
            <el-date-picker
              v-model="loanTime"
              type="daterange"
              value-format="yyyy-MM-dd"
              range-separator="至"
              start-placeholder="开始日期"
              end-placeholder="结束日期">
            </el-date-picker></div>
        </el-col>
        <el-col :span="7">
          <div>提现状态：
            <el-select v-model="depositStatus" placeholder="请选择">
              <el-option :key="'全部'" :label="'全部'" :value="'5'"></el-option>
              <el-option :key="'未提现'" :label="'未提现'" :value="'4'"></el-option>
              <el-option :key="'提现失败'" :label="'提现失败'" :value="'2'"></el-option>
            </el-select>
          </div>
        </el-col>
        <el-col :span="7">
          <div>推荐码：
            <el-select v-model="referralCode" placeholder="请选择">
              <el-option :key="'全部'" :label="'全部'" :value="''"></el-option>
              <el-option :key="index" :label="item" :value="item" v-for="(item, index) in getAllRecoCode"></el-option>
            </el-select>
          </div>
        </el-col>
      </el-row>
      <el-row class="screening" v-else>
        <el-col :span="9">
          <div>放款时间：
            <el-date-picker
              v-model="loanTime"
              type="daterange"
              value-format="yyyy-MM-dd"
              range-separator="至"
              start-placeholder="开始日期"
              end-placeholder="结束日期">
            </el-date-picker></div>
        </el-col>
        <el-col :span="6">
          <div>推荐码：
            <el-select v-model="referralCode" placeholder="请选择">
              <el-option :key="'全部'" :label="'全部'" :value="''"></el-option>
              <el-option :key="index" :label="item" :value="item" v-for="(item, index) in getAllRecoCode"></el-option>
            </el-select>
          </div>
        </el-col>
        <el-col :span="9">
          <div>提现时间：
            <el-date-picker
              v-model="loanTime1"
              type="daterange"
              value-format="yyyy-MM-dd"
              range-separator="至"
              start-placeholder="开始日期"
              end-placeholder="结束日期">
            </el-date-picker></div>
        </el-col>
      </el-row>
      <el-row class="refreshButton">
        <el-col :span="24">
          <div>
            <el-button type="primary" @click="getLendingOrderList">查询</el-button>
            <el-button @click="resetQuery">重置</el-button>
          </div>
        </el-col>
      </el-row>
      <el-row class="statistics" v-if="tabActive==0">
        <el-col :span="24">
          <div><i class="el-icon-warning"></i><p>已选择<span>{{selectNum}}</span>项</p><p>提现金额总计<span>{{withdrawalAmount}}</span>元</p>
            <el-button type="text" @click="toggleSelection()">清空</el-button>
            <el-button type="text" @click="withdrawalClick('W')">提现</el-button>
            <el-button type="text" @click="withdrawalClick('WA')">全部提现</el-button>
          </div>
        </el-col>
      </el-row>
      <el-row class="statistics" v-else>
        <el-col :span="24">
          <div><i class="el-icon-warning"></i><p>已选择<span>{{selectNum}}</span>项</p><p>提现金额总计<span>{{withdrawalAmount}}</span>元</p>
            <span @click="toggleSelection()">清空</span>
            <span @click="exportListClick()">导出</span>
          </div>
        </el-col>
      </el-row>
      <el-table
        ref="multipleTable"
        :data="tableData3"
        tooltip-effect="dark"
        @selection-change="handleSelectionChange"
        style="width: 100%">
        <el-table-column
          type="selection">
        </el-table-column>
        <el-table-column
          prop="loanCode"
          label="订单编号">
        </el-table-column>
        <el-table-column
          prop="realName"
          label="姓名">
        </el-table-column>
        <el-table-column
          prop="phone"
          label="手机号" show-overflow-tooltip>
        </el-table-column>
        <el-table-column
          prop="recoCode"
          label="推荐码" show-overflow-tooltip>
        </el-table-column>
        <el-table-column
          prop="name"
          label="产品名称" show-overflow-tooltip>
        </el-table-column>
        <el-table-column
          prop="planName"
          label="方案名称">
        </el-table-column>
        <el-table-column
          prop="withdrawMoney"
          label="可提现金">
        </el-table-column>
        <el-table-column
          prop="loanMoney"
          label="借款金额">
        </el-table-column>
        <el-table-column
          prop="withdrawStatus"
          label="提现状态">
        </el-table-column>
        <el-table-column
          prop="applicationDate"
          label="申请时间" show-overflow-tooltip>
        </el-table-column>
        <el-table-column
          prop="loanDate"
          label="放款时间" show-overflow-tooltip>
        </el-table-column>
        <el-table-column
          prop="withdrawDate"
          label="提现时间" show-overflow-tooltip>
        </el-table-column>
        <el-table-column
          prop="bankCardMsg"
          label="收款银行卡" show-overflow-tooltip>
        </el-table-column>
      </el-table>
      <el-row>
        <el-col :span="24" class="paging">
          <div>
            <p>借款总金额：<span>¥{{borrowTotal}}</span></p>
            <el-pagination
              @current-change="changePageFun"
              @prev-click="changePageFun"
              @next-click="changePageFun"
              background
              layout="prev, pager, next"
              :total="totalListNum">
            </el-pagination>
          </div>
        </el-col>
      </el-row>
    </template>
  </div>
</template>

<script>
import Vue from 'vue'
import Axios from '@/config/axios'
import APIURL from '@/config/APIURL'
Vue.prototype.$ajax = Axios
export default {
  name: 'main-content',
  data () {
    return {
      // 账户信息
      huaxiazhanghu: '',
      labizhanghu: '',
      qiyejiancheng: '',
      qiyemingcheng: '',
      kaihushijian: '',
      yinhangka: '',
      zhengjianleixing: '',
      zhengjianhaoma: '',
      zhizhaobianhao: '',
      dengjihao: '',
      farenxingming: '',
      farenshenfenzheng: '',
      farenshoujihao: '',
      openaccstatus: '',
      acctsts: '',
      openmsg: '',
      acctstsmsg: '',
      // 账户信息end
      value: '',
      loanTime: '',
      loanTime1: '',
      depositStatus: '全部',
      referralCode: '全部',
      tabActive: '0',
      selectNum: '0',
      withdrawalAmount: 0,
      borrowTotal: 0,
      selectOrder: [],
      agencyObject: '',
      getLendingOrderListObject: {
        'appId': 'org_crm_merchant',
        'bankCardNo': '',
        'bankMsg': '',
        'businessType': '',
        'certNo': '',
        'certType': '',
        'depositAcct': '',
        'inCashEndTime': '',
        'inCashStartTime': '',
        'legalCertNoDetail': '',
        'loanEndTime': '',
        'loanStartTime': '',
        'loginPhone': sessionStorage.getItem('phone'),
        'orderIds': '',
        'orgId': '',
        'pageNo': 1,
        'pageSize': 10,
        'recCode': '',
        'recoCode': '',
        'reqTime': this.getNowFormatDate(),
        'sortName': '',
        'sortType': '',
        'subAcct': '',
        'token': sessionStorage.getItem('token'),
        'tradeStatus': '',
        'tradeType': '',
        'type': 5,
        'withdrawType': ''
      },
      getAllRecoCodeObject: {
        'appId': 'org_crm_merchant',
        'loginPhone': sessionStorage.getItem('phone'),
        'reqTime': this.getNowFormatDate(),
        'token': sessionStorage.getItem('token'),
        'businessType': '',
        'legalCertNoDetail': '',
        'certNo': '',
        'certType': '',
        'depositAcct': '',
        'subAcct': ''
      },
      withdrawalObject: {
        'appId': 'org_crm_merchant',
        'loginPhone': sessionStorage.getItem('phone'),
        'reqTime': this.getNowFormatDate(),
        'token': sessionStorage.getItem('token'),
        'withdrawType': '',
        'type': 5,
        'loanStartTime': '',
        'loanEndTime': '',
        'recCode': '',
        'certType': '',
        'certNo': '',
        'businessType': '',
        'depositAcct': '',
        'subAcct': '',
        'orderIds': '',
        'recoCode': ''

      },
      tableData3: [],
      multipleSelection: [],
      totalListNum: 1
    }
  },
  created () {
    this.getOrgCodes()
    sessionStorage.setItem('AcctInfo', null)
  },
  methods: {
    changebankcard () {
      this.$router.push({ path: 'changebankcard' })
    },
    changebankphone () {
      this.$router.push({ path: 'changebankphone' })
    },
    openAcc () {
      this.$router.push({ path: 'openAcc' })
    },
    changeTab (index) {
      if (index === 0) this.getLendingOrderListObject.type = 5
      if (index === 1) this.getLendingOrderListObject.type = 0
      if (index === 2) this.getLendingOrderListObject.type = 1
      this.getLendingOrderList()
      this.tabActive = index
      this.loanTime = ''
      this.loanTime1 = ''
      this.depositStatus = '全部'
      this.referralCode = '全部'
    },
    toggleSelection () {
      this.$refs.multipleTable.clearSelection()
    },
    handleSelectionChange (selection) {
      this.selectNum = selection.length
      this.withdrawalAmount = 0
      this.selectOrder.splice(0)
      selection.forEach((item) => {
        this.withdrawalAmount += Number(item.withdrawMoney)
        this.selectOrder.push(item.id)
      })
      this.withdrawalObject.orderIds = this.selectOrder
    },
    resetQuery () {
      this.loanTime = ''
      this.loanTime1 = ''
      this.depositStatus = '全部'
      this.referralCode = '全部'
    },
    changePassword (param) {
      if (param === 'modify') {
        this.getLendingOrderListObject.tradeType = 'U4'
      } else if (param === 'reset') {
        this.getLendingOrderListObject.tradeType = 'U5'
      }
      this.$ajax.post(APIURL.modifyDepositAcctPwd, this.getLendingOrderListObject).then((res) => {
        if (res.data.code === 0) {
          location.href = res.data.data.jumpUrl
        } else {
          alert(res.data.msg)
        }
      })
    },
    exportListClick () {
      const downAllExcelNumUrl = APIURL.downAllExcelNum
      this.$ajax.post(downAllExcelNumUrl, this.getLendingOrderListObject).then((res) => {
        if (res.data.code === 0) {
          this.$ajax.get(APIURL.downAllExcel, this.getLendingOrderListObject).then((res) => {
            if (res.data.code === 0) {
            } else {
              alert(res.data.msg)
            }
          })
        } else {
          alert(res.data.msg)
        }
      })
    },
    withdrawalClick (param) {
      this.$confirm(`<p style="text-align: left">·提现金额：${param === 'W' ? this.withdrawalAmount : this.borrowTotal}元<br/>·订单数：${param === 'W' ? this.selectNum : this.totalListNum}笔<br/>·银行卡：${this.yinhangka}</p>`, '提现确认', {
        dangerouslyUseHTMLString: true,
        confirmButtonText: '立即提现',
        cancelButtonText: '等一等',
        type: 'info',
        center: true
      }).then(() => {
        this.withdrawalObject.withdrawType = param
        const withdrawalUrl = APIURL.withdrawSelectedOrders
        this.$ajax.post(withdrawalUrl, this.withdrawalObject).then((res) => {
          if (res.data.code === 0) {
            location.href = res.data.data.jumpUrl
          } else {
            alert(res.data.msg)
          }
        })
      }).catch(() => {
      })
    },
    getNowFormatDate () {
      let date = new Date()
      let seperator1 = '-'
      let seperator2 = ':'
      let month = date.getMonth() + 1
      let strDate = date.getDate()
      if (month >= 1 && month <= 9) {
        month = '0' + month
      }
      if (strDate >= 0 && strDate <= 9) {
        strDate = '0' + strDate
      }
      let currentdate = date.getFullYear() + seperator1 + month + seperator1 + strDate + ' ' + date.getHours() + seperator2 + date.getMinutes() + seperator2 + date.getSeconds()
      return currentdate
    },
    // 获取所属机构
    getOrgCodes () {
      const getOrgCodesUrl = APIURL.queryOrgCodes
      this.$ajax.post(getOrgCodesUrl, {
        appId: 'org_crm_merchant',
        loginPhone: sessionStorage.getItem('phone'),
        reqTime: this.getNowFormatDate(),
        token: sessionStorage.getItem('token')
      }).then((res) => {
        if (res.data.code === 0) {
          this.agencyObject = res.data.data
          this.value = res.data.data[0]['recoCode']
        } else {
          alert(res.data.msg)
        }
      })
    },
    getAllRecoCode () {
      const getAllRecoCodeUrl = APIURL.getAllRecoCode
      this.$ajax.post(getAllRecoCodeUrl, this.getAllRecoCodeObject).then((res) => {
        if (res.data.code === 0) {
          return res.data.data
        } else {
          alert(res.data.msg)
        }
      })
    },
    getLendingOrderList () {
      this.getLendingOrderListObject.recoCode = sessionStorage.getItem('recoCode')
      const getLendingOrderListUrl = APIURL.getLendingOrderList
      this.$ajax.post(getLendingOrderListUrl, this.getLendingOrderListObject).then((res) => {
        if (res.data.code === 0) {
          // 总借款额
          this.borrowTotal = res.data.data.amount
          // 总条数
          this.totalListNum = res.data.data.totalCount
          this.tableData3 = res.data.data.orders
        } else {
          alert(res.data.msg)
        }
      })
    },
    changePageFun (val) {
      this.getLendingOrderListObject.pageNo = val
      this.getLendingOrderList()
    }
  },
  watch: {
    value: function (newValue, oldValue) {
      sessionStorage.setItem('recoCode', newValue)
      this.withdrawalObject.recoCode = newValue
      const getMerchantDepositAcctInfoUrl = APIURL.queryMerchantDepositAcctInfo
      this.$ajax.post(getMerchantDepositAcctInfoUrl, {
        appId: 'org_crm_merchant',
        loginPhone: sessionStorage.getItem('phone'),
        recoCode: newValue,
        reqTime: this.getNowFormatDate(),
        token: sessionStorage.getItem('token')
      }).then((res) => {
        if (res.data.code === 0) {
          // 提现参数
          this.withdrawalObject.certType = res.data.data.certType
          this.withdrawalObject.certNo = res.data.data.certNo
          this.withdrawalObject.businessType = res.data.data.businessType
          this.withdrawalObject.depositAcct = res.data.data.depositAcct
          this.withdrawalObject.subAcct = res.data.data.subAcct
          // 提现列表参数
          this.getLendingOrderListObject.bankMsg = res.data.data.bankMsg
          this.getLendingOrderListObject.businessType = res.data.data.businessType
          this.getLendingOrderListObject.certNo = res.data.data.certNo
          this.getLendingOrderListObject.certType = res.data.data.certType
          this.getLendingOrderListObject.depositAcct = res.data.data.depositAcct
          this.getLendingOrderListObject.subAcct = res.data.data.subAcct
          this.getLendingOrderListObject.certType = res.data.data.certType
          this.getLendingOrderListObject.legalCertNoDetail = res.data.data.legalCertNoDetail
          // 推荐码参数
          this.getAllRecoCodeObject.businessType = res.data.data.businessType
          this.getAllRecoCodeObject.legalCertNoDetail = res.data.data.legalCertNoDetail
          this.getAllRecoCodeObject.certNo = res.data.data.certNo
          this.getAllRecoCodeObject.certType = res.data.data.certType
          this.getAllRecoCodeObject.depositAcct = res.data.data.depositAcct
          this.getAllRecoCodeObject.subAcct = res.data.data.subAcct
          sessionStorage.setItem('AcctInfo', JSON.stringify(res.data.data))
          this.huaxiazhanghu = res.data.data.depositAcct
          this.labizhanghu = res.data.data.subAcct
          this.qiyejiancheng = res.data.data.orgShortName
          this.qiyemingcheng = res.data.data.orgName
          this.kaihushijian = res.data.data.openAcctTime
          this.yinhangka = res.data.data.bankMsg
          this.zhengjianleixing = res.data.data.certTypeMsg
          this.zhengjianhaoma = res.data.data.certNo
          this.zhizhaobianhao = res.data.data.busiLiceNo
          this.dengjihao = res.data.data.taxNo
          this.farenxingming = res.data.data.legalName
          this.farenshenfenzheng = res.data.data.legalCertNo
          this.farenshoujihao = res.data.data.legalPhone
          this.openaccstatus = res.data.data.openStatus
          this.openmsg = res.data.data.openMsg
          this.acctstsmsg = res.data.data.acctStsMsg
          if (this.openaccstatus === 'P02') {
            this.getAllRecoCode()
            this.getLendingOrderList()
          }
        } else {
          alert(res.data.msg)
        }
      })
    },
    loanTime: function (newValue, oldValue) {
      this.getLendingOrderListObject.loanStartTime = newValue[0]
      this.getLendingOrderListObject.loanEndTime = newValue[1]
      this.withdrawalObject.loanStartTime = newValue[0]
      this.withdrawalObject.loanEndTime = newValue[1]
    },
    depositStatus: function (newValue, oldValue) {
      this.getLendingOrderListObject.type = newValue
    },
    referralCode: function (newValue, oldValue) {
      this.getLendingOrderListObject.recCode = newValue
      this.withdrawalObject.recCode = newValue
    },
    loanTime1: function (newValue, oldValue) {
      this.getLendingOrderListObject.inCashStartTime = newValue[0]
      this.getLendingOrderListObject.inCashEndTime = newValue[1]
    }
  },
  computed: {
  },
  components: {
  }
}
</script>
<style lang="scss" scoped>
  .main-box{
    .topTip{
      div{
        line-height: 2.5;
        border: 1px solid #b7eb8f;
        background-color: #f6ffed;
        font-size: 14px;
        color: rgba(0,0,0,.65);
        border-radius: 4px;
        i{
          padding-right: 20px;
          color: #79af56;
        }
      }
    }
    .accInfo{
      border-bottom: 1px solid #ccc;
      .jigou{
        text-align: left;
        color: red;
      }
      .left{
        height: 100px;
        div{
          img{
            float: left;
            display: inline-block;
            margin-top: 10px;
            width: 80px;
            height: 80px;
          }
          div{
            float: left;
            display: inline-block;
            width: 80%;
            height: 60px;
            margin-top: 23px;
            span{
              display: block;
              height: 25px;
              padding-left: 20px;
