      }
      .right{
        height: 100px;
        .top{
          height: 50px;
          line-height: 85px;
        }
        .bottom{
          height: 50px;
          line-height: 60px;
          font-size: 12px;
          text-align: right;
          padding-right: 40px;
          p{
            display: inline-block;
            height: 30px;
            line-height: 30px;
            span{
              color: #2808b5;
            }
          }
        }
      }
    }
    .accInfo1{
      p{
        text-align: left;
        line-height: 50px;
        padding-left: 50px;
      }
    }
    .tab{
      div{
        height: 50px;
        line-height: 50px;
        background-color: #f8f8f8;
        p{
          float: left;
          display: inline-block;
          width: 100px;
          height: 50px;
          line-height: 50px;
          font-size: 16px;
          box-sizing: border-box;
          &.active{
            color: #1890ff;
            border-bottom: 2px solid #1890ff;
          }
        }
      }
    }
    .screening{
      height: 80px;
      line-height: 80px;
    }
    .refreshButton{
      div{
        float: right;
        height: 50px;
        line-height: 50px;
        padding-right: 30px;
      }
    }
    .statistics{
      height: 40px;
      line-height: 40px;
      text-align: left;
      div{
        line-height: 2.5;
        border: 1px solid #91d5ff;
        background-color: #e6f7ff;
        font-size: 14px;
        color: rgba(0,0,0,.65);
        border-radius: 4px;
      }
      i{
        padding:0 20px;
        color: #1890ff;
      }
      span{
        color: #1890ff;
      }
      p{
        display: inline-block;
        margin-right: 20px;
        span{
          color: #1890ff;
        }
      }
    }
    .paging{
      line-height: 60px;
      p{
        float: left;
        display: inline-block;
        line-height: 60px;
        span{
          color: red;
        }
      }
      .el-pagination{
        float: right;
        display: inline-block;
        width: 500px;
        line-height: 60px;
        padding-top: 20px;
      }
    }
  }
</style>



