<template>
  <div class="index-box">
    <el-container style="height:100vh;">
      <el-aside width="220px"  :class="{hideSidebar:!sidebar.opened}">
        <el-menu>
          <el-menu-item index="1"><img src="/static/img/logo.png"><span class="title">蜡笔分期商家版</span></el-menu-item>
          <el-menu-item index="2">
            <el-tooltip v-if="!sidebar.opened" class="item" effect="dark" content="蜡笔工作台" placement="right">
              <span class="el-icon-star-off"></span>
            </el-tooltip>
            <span v-else class="el-icon-star-off"></span>
            <span class="title" @click="gztJump(0)">蜡笔工作台</span><i class="el-icon-arrow-right"></i>
          </el-menu-item>
          <el-menu-item index="3">
            <el-tooltip v-if="!sidebar.opened" class="item" effect="dark" content="订单管理" placement="right">
              <span class="el-icon-star-off"></span>
            </el-tooltip>
            <span v-else class="el-icon-star-off"></span><span class="title" @click="gztJump(1)">订单管理</span><i class="el-icon-arrow-right"></i>
          </el-menu-item>
          <el-menu-item index="4">
            <el-tooltip v-if="!sidebar.opened" class="item" effect="dark" content="财务管理" placement="right">
              <span class="el-icon-star-off"></span>
            </el-tooltip>
            <span v-else class="el-icon-star-off"></span><span class="title" @click="gztJump(2)">财务管理</span><i class="el-icon-arrow-right"></i>
          </el-menu-item>
          <el-menu-item index="5" class="selet">
            <el-tooltip v-if="!sidebar.opened" class="item" effect="dark" content="账号提现" placement="right">
              <span class="el-icon-star-off"></span>
            </el-tooltip>
            <span v-else class="el-icon-star-off"></span><span class="title">账号提现</span><i class="el-icon-arrow-right"></i>
          </el-menu-item>
        </el-menu>
      </el-aside>
      <el-container>
        <el-header>
          <hamburger class="hamburger-container" :toggleClick="toggleSideBar" :isActive="sidebar.opened"></hamburger>
          <el-dropdown class="avatar-container right-menu-item" trigger="click">
            <div class="avatar-wrapper">
              <p class="user-avatar" style="width: 80px">{{realName}}</p>
              <i class="el-icon-caret-bottom"></i>
            </div>
            <el-dropdown-menu slot="dropdown">
              <el-dropdown-item @click.native="loginOut">
                Logout
              </el-dropdown-item>
            </el-dropdown-menu>
          </el-dropdown>
        </el-header>
        <el-main>
          <router-view/>
        </el-main>
      </el-container>
    </el-container>
  </div>
</template>

<script>
import Vue from 'vue'
import Axios from 'axios'
import APIURL from '@/config/APIURL'
import { mapGetters } from 'vuex'
import Hamburger from '@/components/Hamburger'
import MainContent from '@/components/MainContent'
import ChangeBankCard from '@/components/ChangeBankCard'
import OpenAcc from '@/components/OpenAcc'
Axios.defaults.timeout = 5000
Axios.defaults.withCredentials = true
Axios.defaults.headers.post['Content-Type'] = 'application/json; charset=utf-8'
Vue.prototype.$ajax = Axios
export default {
  name: 'index',
  data () {
    return {
      realName: sessionStorage.getItem('realName')
    }
  },
  methods: {
    gztJump (num) {
      location.href = `${window.location.href}mrs/workbench.html?accType=${num}`
    },
    toggleSideBar () {
      this.$store.dispatch('toggleSideBar')
    },
    getNowFormatDate () {
      let date = new Date()
      let seperator1 = '-'
      let seperator2 = ':'
      let month = date.getMonth() + 1
      let strDate = date.getDate()
      if (month >= 1 && month <= 9) {
        month = '0' + month
      }
      if (strDate >= 0 && strDate <= 9) {
        strDate = '0' + strDate
      }
      let currentdate = date.getFullYear() + seperator1 + month + seperator1 + strDate + ' ' + date.getHours() + seperator2 + date.getMinutes() + seperator2 + date.getSeconds()
      return currentdate
    },
    loginOut () {
      const loginOutUrl = APIURL.loginOut
      this.$ajax.post(loginOutUrl, {
        appId: 'org_crm_merchant',
        loginPhone: sessionStorage.getItem('phone'),
        reqTime: this.getNowFormatDate(),
        token: sessionStorage.getItem('token')
      }).then((res) => {
        if (res.data.code === 0) {
          location.href = 'login.html'
        } else {
          alert(res.data.msg)
        }
      })
    }
  },
  computed: {
    ...mapGetters([
      'sidebar'
    ])
  },
  components: {
    Hamburger,
    MainContent,
    ChangeBankCard,
    OpenAcc
  }
}
</script>
<style lang="scss" scoped>
  .hideSidebar{
    width: 66px !important;
    .el-icon-arrow-right{
      display: none
    }
    .title{
      display: none
    }
  }
  .el-header{
    background-color: #efeff4;
    color: #333;
    text-align: center;
    line-height: 60px;
    .hamburger-container {
      line-height: 58px;
      height: 50px;
      float: left;
      padding: 5px 10px;
    }
    .avatar-container {
      height: 50px;
      margin-right: 30px;
      float: right;
      .avatar-wrapper {
        cursor: pointer;
        margin-top: 5px;
        position: relative;
        .user-avatar {
          width: 40px;
          height: 40px;
          border-radius: 50%;
        }
        .el-icon-caret-bottom {
          position: absolute;
          right: -20px;
          top: 25px;
          font-size: 12px;
        }
      }
    }
  }

  .el-aside {
    background-color: #00152a;
    color: #fff;
    .el-menu-item{
      background-color: #00152a;
      color: #fff;
      font-size: 16px;
      &.selet{
        background-color: #507EF2;
      }
      span{
        padding-right: 10px;
      }
      img{
        width: 35px;
        height: 45px;
        padding-right: 5px;
      }
      i{
        float: right;
        font-size: 18px;
        color: #fff;
        line-height:54px;
      }
    }
  }

  .el-main {
    background-color: #ffffff;
    color: #333;
    text-align: center;
    line-height: 160px;
  }
</style>



